export default {
  data() {
    return {}
  },
  props: {
    hasIndex: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: 'list-view'
    },
    list: {
      type: Array,
      default: () => []
    },
    twoColumn: {
      type: Boolean,
      default: false
    },
    imgHeight: {
      type: Number,
      default: 100
    },
    hasImg: {
      type: Boolean,
      default: false
    },
    keyInfo: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    picWrapStyle() {
      return {
        height: this.imgHeight * this.__DPR + 'px'
      }
    },
    bind_key() {
      return {
        title: 'title',
        desc: 'desc',
        picUrl: 'picUrl',
        ...this.keyInfo
      }
    }
  },
  created() {
    this.$options.name = this.name
  },
  methods: {
    selectPlay(item, index) {
      switch (this.name) {
        case 'music-list':
          this.$emit('music-selected', item, index)
          break
        case 'album-list':
          this.$emit('album-selected', item)
          break
        case 'mv-list':
          this.$emit('mvSelected', item)
      }
    }
  }
}



